<template>
  <div class="details">
    <div class="time-desc">数据统计日期：{{params.timeStr}}</div>
    <div class="goods flex ac js">
      <CustomImg width="1.6rem" height="1.6rem" fit="cover" round :src="params.head_pic"></CustomImg>
      <div class="item goods-name">
        <div>{{params.nickname | longtext}}的团队</div>
        <div>人数：{{member}}人</div>
      </div>
    </div>
    <WholeData :nums="nums"></WholeData>
    <div class="rank">
      <div class="rank-head">
        <MenuBar ref="menuBar" :list="menus" @change="menuChange"></MenuBar>
        <div class="rank-title flex ac js">
          <div>{{params.type === 'sale_num' ? '商品销售数量排行' : '商品销售额排行'}}</div>
          <div class="flex ac" @click="toggleSort">
            {{ params.order === 0 ? '按销量由多到少排序' : '按销量由少到多排序'}}
            <img
              :class="{top: params.order === 1}"
              :src="require('@/assets/bottom.png')"
              alt
            />
          </div>
        </div>
      </div>
      <div class="rank-list">
        <InfiniteList
          ref="list"
          v-slot="slotProps"
          api="teamManage/getTeamGoodsSaleList"
          :params="listParams"
          :formatData="formatData"
        >
          <div
            class="list-item flex column"
            v-for="(item, index) in slotProps.list"
            :key="item.goods_id"
            @click="toGoods(item.goods_id)"
          >
            <div class="detail item flex ac js">
              <CustomImg
                width="1.6rem"
                height="1.6rem"
                fit="cover"
                :src="item.goods_info.original_img"
              />
              <div class="item goods-name">{{item.goods_info.goods_name}}</div>
              <div v-if="params.type === 'sale_money'" class="goods-num">
                {{item.sale_money}}
                <span>元</span>
              </div>
              <div v-if="params.type === 'sale_num'" class="goods-num">
                {{item.sale_num}}
                <span>件</span>
              </div>
              <img v-if="index === 0" class="rank-icon" :src="require('@/assets/rank-one.png')" alt />
              <img v-if="index === 1" class="rank-icon" :src="require('@/assets/rank-two.png')" alt />
              <img
                v-if="index === 2"
                class="rank-icon"
                :src="require('@/assets/rank-three.png')"
                alt
              />
            </div>
            <div v-if='goodsVisible' class="list-item-btn text-center">查看商品详情</div>
          </div>
        </InfiniteList>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import WholeData from "@/components/WholeData";
import MenuBar from "@/components/MenuBar";
import InfiniteList from "@/components/InfiniteList";

export default {
  components: { WholeData, MenuBar, InfiniteList },
  data() {
    return {
      params: {
        order: 0, // 排序
        type: "sale_num"
      }, // 接口参数,
      nums: [], // 整体数据
      goods: {}, // 商品信息
      member: 0, // 团队人数
      menus: [
        { name: "商品销售数量排行", value: "sale_num" },
        { name: "商品销售额排行", value: "sale_money" }
      ] // 排行榜tab
    };
  },
  computed: {
    listParams() {
      const params = { ...this.params };
      delete params.timeStr;
      delete params.head_pic;
      delete params.nickname;
      return params;
    },
    ...mapGetters({ goodsVisible: "config/goodsVisible" })
  },
  created() {
    const params = this.$route.query;
    this.params = {
      ...this.params,
      ...params
    };
    this.$store.dispatch("config/getConfig");
    console.log(111)
  },
  mounted() {
    this.$refs.menuBar.active = this.params.type === "sale_num" ? 0 : 1;
    this.$refs.list.resetData();
  },
  methods: {
    // 排行榜菜单切换
    menuChange(index, value) {
      this.params = {
        ...this.params,
        type: value
      };
      this.$refs.list.resetData();
    },
    // 排序
    toggleSort() {
      const params = this.params;
      this.params = {
        ...params,
        order: params.order === 1 ? 0 : 1
      };
      this.$refs.list.resetData();
    },
    // 数据格式化
    formatData(res) {
      const { sale_num, sale_money, member, list } = res;
      const { data, current_page, last_page } = list;
      const nums = [];
      nums.push(
        { name: "总销量(件)", value: sale_num || 0 },
        { name: "销售额(元)", value: sale_money }
      );
      this.nums = nums;
      this.member = member;
      return {
        data,
        current_page,
        last_page
      };
    },
    // 商品详情
    toGoods(id) {
      if (!this.goodsVisible) {
        return;
      }
      const invite_code = JSON.parse(localStorage.getItem("userInfo"))
        .invite_code;
      const url = `${process.env.VUE_APP_CHUNGOU_H5}/goodsDetail?id=${id}&invite_code=${invite_code}`;
      location.href = url;
    }
  }
};
</script>

<style lang="less" scoped>
.time-desc {
  height: 40px;
  line-height: 40px;
  padding-left: 13px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  margin: 0 0 10px;
  color: #777;
  font-size: 14px;
}

.rank {
  background: #fff;
}

.rank-head {
  overflow: hidden;
  background: #fff;
  padding: 10px 13px 0;
}

.rank-title {
  box-sizing: border-box;
  width: 349px;
  height: 24px;
  background: rgba(254, 250, 243, 1);
  color: #282828;
  font-size: 13px;
  line-height: 24px;
  margin: 18px auto;
  padding: 0 10px;
  img {
    width: 11px;
    margin-left: 6px;
  }
}

.goods {
  background: #fff;
  width: 349px;
  height: 85px;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 1);
  border-radius: 6px;
  margin: 0 auto;
  padding: 0 10px;
}

.goods-name {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  padding: 0 10px;
  div:last-child {
    color: #777;
    font-size: 12px;
    margin-top: 5px;
  }
}

.goods-btn {
  width: 60px;
  height: 20px;
  line-height: 20px;
  border-radius: 10px;
  border: 1px solid rgba(238, 238, 238, 1);
  color: #ffc500;
  font-size: 12px;
}

.top {
  transform: rotate(180deg);
}

.list-item {
  box-sizing: border-box;
  width: 349px;
  height: 114px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  border-radius: 8px;
  margin: 10px auto;
}

.list-item-btn {
  height: 34px;
  line-height: 34px;
  color: #f6d9b4;
  font-size: 12px;
  background: rgba(254, 250, 243, 1);
  border-radius: 0 0 8px 8px;
}

.detail {
  padding: 0 10px;
  position: relative;
}

.rank-icon {
  width: 25px;
  position: absolute;
  top: 0;
  right: 13px;
}

.team-img {
  margin: 0 10px;
}

.goods-num {
  color: #252525;
  font-size: 18px;
  font-weight: 500;
  span {
    font-size: 14px;
  }
}
</style>